import { gsap, Power4 } from 'gsap'


export default {
  data() {
    return {
      proyectos: null,
      transY: [-8, 8],
      scale: [0.6, 1.1],
      tl_preload_salida: gsap.timeline({
        paused: true,
        delay: process.env.NODE_ENV === 'development' ? 1 : 5,
        ease: Power4.easeInOut,
        onStart: () => {
          if (!this.$isMobile()) {
            gsap.fromTo(
              '.marquee-close',
              {
                clipPath: 'inset(0% 0% 0% 0%)',
              },
              { clipPath: 'inset(0% 0% 0% 100%)', duration: 1 / 2 }
            )
          }
        },
        onUpdate: () => {},
        onComplete: () => {
          console.log('⏱️ PRELOAD -> tl_preload_salida onComplete')
          // this.$store.commit('app/setPreload', true)
        },
      }),
      tl_preload_arrow: gsap.timeline({
        ease: Power4.easeInOut,
        repeat: -1,
        onUpdate: () => {},
        onComplete: () => {},
      }),
    }
  },
  fetchOnServer: false,
  async fetch() {
    try {
      const home = await this.$storyapi.get(`cdn/stories/home`, {
        version: 'published',
        resolve_relations: 'page.proyectos,page.destacado',
      })
      this.proyectos = home.data.story.content.proyectos
    } catch (e) {
      console.error('ERROR Home', e)
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.tl_preload_salida.play()
    })
    this.$Lazyload.$on(
      'loaded',
      (
        {
          bindType,
          el,
          naturalHeight,
          naturalWidth,
          $parent,
          src,
          loading,
          error,
        },
        formCache
      ) => {
        if (el.classList.contains('marquee_proy') && !this.$isMobile()) {
          gsap.fromTo(
            el,
            {
              y: 0,
              scale: 0.5,
              autoAlpha: 0,
            },
            {
              autoAlpha: 1,
              y: () => {
                const sc = (
                  Math.random() * (this.transY[1] - this.transY[0]) +
                  this.transY[0]
                ).toFixed(4)
                return sc
              },
              scale: () => {
                const sc = (
                  Math.random() * (this.scale[1] - this.scale[0]) +
                  this.scale[0]
                ).toFixed(4)
                return sc
              },
              duration: () => {
                const dur = (Math.random() * (1 - 0.5) + 0.5).toFixed(4)
                return dur
              },
              ease: Power4.easeInOut,
              // onStart: () => {
              //   window.dispatchEvent(new Event('resize'))
              // },
            }
          )
        }
      }
    )
    this.initTimelines()
  },
  methods: {
    initTimelines() {
      const abierto = {
        clipPath: 'inset(0% 0% 0% 0%)',
        // webkitClipPath: 'inset(0% 0% 0% 0%)',
      }
      const tiempoEntrada = 1

      const cerradoToLeft = {
        clipPath: 'inset(0% 0% 0% 100%)',
        // webkitClipPath: 'inset(0% 0% 0% 100%)',
      }

      this.tl_preload_salida.addLabel('salida', `+=0`)
      this.tl_preload_salida.fromTo(
        '.capa_2',
        abierto,
        { ...cerradoToLeft, delay: 0, duration: tiempoEntrada },
        'salida+=0'
      )
      this.tl_preload_salida.fromTo(
        '.close',
        abierto,
        { ...cerradoToLeft, duration: tiempoEntrada / 2 },
        `salida-=${tiempoEntrada * 0.1}`
      )

      this.tl_preload_arrow.fromTo(
        '.arrow',
        {
          x: '-100vw',
          y: '100vw',
        },
        { x: '100vw', y: '-100vw', delay: 0, duration: 2 }
      )
    },
  },
}
